<template>
  <div>
    <div class="mobile">
      <div class="d-flex justify-content-between">
        <v-btn small @click="openModel">Salary</v-btn>
        <v-btn small  @click="Qualification">Qualification</v-btn>
      </div>
    </div>
    <div class="jobside shadow desktop">
      <div class="h5 sec1 mt-5">Salary</div>
      <div class="range-slider sec1 bg-white p-2">
        <span class="d-flex">
          <input :value="range[0]" class="me-2 form-control text-center"  @change="$set(range, 0, $event)">
          <input :value="range[1]" class="me-2 form-control text-center"  @change="$set(range, 1, $event)">
        </span>
        <v-range-slider v-model="range" :max="max" :min="min" class="align-center"></v-range-slider>
      </div>
      <div class="h5 sec1">Qualification</div>
      <b-form-select v-model="selected" :options="options" class="form-select"></b-form-select>
      <v-btn color="success" class="my-2">Go</v-btn>
    </div>

    <v-dialog v-model="showModel" persistent>
      <v-card>
         <span class="d-flex justify-content-between">
            <v-card-title>Salary</v-card-title>
            <v-icon @click="showModel = false" class="me-3">mdi-close</v-icon>
         </span>
        <hr class="m-0">
        <v-card-text>
          <v-range-slider
              v-model="range" :max="max" :min="min" class="align-center"></v-range-slider>
             <span class="d-flex">
                <input :value="range[0]" class="me-2 form-control text-center"  @change="$set(range, 0, $event)">
                <input :value="range[1]" class="me-2 form-control text-center"  @change="$set(range, 1, $event)">
              </span>
        </v-card-text>
            <div class="text-center">
               <b-button class="mb-3" variant="success" type="submit">Go</b-button>
            </div>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog" persistent>
      <v-card>
         <span class="d-flex justify-content-between">
            <v-card-title>Qualification</v-card-title>
            <v-icon @click="dialog = false" class="me-3">mdi-close</v-icon>
         </span>
        <hr class="m-0">
        <v-card-text>
          <b-form-select v-model="selected" :options="options" class="form-select mt-4"></b-form-select>
        </v-card-text>
            <div class="text-center">
               <b-button class="mb-3" variant="success" type="submit">Go</b-button>
            </div>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: "JobSidebar",
  data() {
    return{
      showModel: false,
      dialog: false,
      selected: null,
      min: 0,
      max: 120000,
      range: [500, 10000],
      options: [
        { value: null, text: '--Select--' },
        { value: 'a', text: '12TH' },
        { value: 'b', text: 'BA' },
        { value: { C: '3PO' }, text: 'Diploma' },
      ]
    }
  },
  methods: {
    openModel() {
      this.showModel = true;
    },
    Qualification() {
      this.dialog = true;
    }
  }
}
</script>

<style scoped>
.jobside{
  background: #e5e5e5;
  padding: 10px;
}
.sec1{
  background: #f2f2f2;
  padding: 5px 15px;
  margin-bottom: 20px;
}

</style>